* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header {
  margin-left: 40px;
  margin-bottom: 40px;

  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo {
  display: flex;
  align-items: flex-end;
  gap: 10px;
}

.log img {
  width: 50px;
  height: 50px;
}

body {
  background-color: rgb(48, 48, 47);
  color: white;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  padding: 40px;
}

.form {
  background-color: rgb(78, 78, 78);
  margin-bottom: 40px;
  padding: 16px 24px;
  display: flex;
  align-items: center;
  gap: 16px;
  border-radius: 70px;
}

.form input {
  background-color: rgb(128, 124, 124);
  padding: 8px 16px;
  border-radius: 20px;
  border: none;
  font-size: 18px;
  color: inherit;
}
.form input:first-child {
  flex-grow: 1;
}

.form select {
  background-color: rgb(128, 124, 124);
  padding: 8px 16px;
  border-radius: 20px;
  border: none;
  font-size: 18px;
  color: inherit;
}

.form span {
  font-size: 18px;
  font-weight: 600;
  margin-right: 12px;
  margin-right: 10px;
}

.form input::placeholder {
  color: rgb(187, 183, 183);
}

@media (max-width: 1000px) {
  .form {
    flex-direction: column;
    gap: 8px;
  }
  .form input:first-child {
    width: 100%;
  }
  .form select {
    width: 100%;
  }
}

.hidden {
  display: none;
}
.main {
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 40px;
}
@media (max-width: 1000px) {
  .main {
    grid-template-columns: 1fr;
    height: 60vh;
  }
}

/* section {
  overflow: scroll;
} */

/*main */
.message {
  text-align: center;
  text-transform: uppercase;
  font-size: 32px;
  font-weight: 600;
  margin-top: 24px;
}
#root {
  max-width: 2000px;
  margin-left: auto;
  margin-right: auto;
}
h1 {
  font-size: 42px;
  text-transform: uppercase;
  text-align: center;
  font-family: "Coiny", sans-serif;
  color: rgb(94, 42, 236);
  margin-bottom: 8px;
}

.category {
  margin-bottom: 16px;
}
.fact {
  font-size: 20px;
  line-height: 1.4;
  margin-bottom: 18px;
  background-color: rgb(78, 78, 78);
  padding: 18px 24px;
  display: flex;
  align-items: center;
  letter-spacing: 3px;
  border-radius: 50px;
  gap: 24px;
}

.disputed {
  color: rgb(247, 68, 45);
  font-weight: bold;
  font-size: 18px;
  margin-right: 10px;
}

@media (max-width: 1200px) {
  .fact {
    background-color: rgb(78, 78, 78);
    flex-direction: column;
    align-items: flex-end;
    gap: 12px;
  }
}
.source {
  color: rgb(187, 183, 183);
  margin-left: 10px;
  transition: 0.3s;
}

.source:hover {
  text-decoration: underline;
  cursor: pointer;
  color: rgb(247, 68, 45);
}

.source:active {
  color: rgb(8, 241, 59);
}

.tag {
  text-transform: uppercase;
  font-size: 14px;
  font-family: "Coiny", sans-serif;
  cursor: pointer;
  border-radius: 20px;
  padding: 10px 10px;
}

ul {
  list-style-type: none;
}

.voteBTN {
  margin-left: auto;
  flex-shrink: 0;
  display: flex;
  gap: 8px;
}
.voteBTN button {
  background-color: rgb(134, 128, 117);
  border: 0;
  font-size: 18px;
  padding: 6px 12px;
  border-radius: 20px;
  color: white;
}
.voteBTN button:hover {
  background-color: rgb(49, 49, 47);
  transform: scale(110%);
  transition: 0.3s;
}
.sideBTN {
  font-family: "Coiny", sans-serif;
  line-height: 1;
  text-transform: uppercase;
  font-size: 17px;
  padding: 16px 0 13px;
  background-image: linear-gradient(
    to right,
    rgb(94, 42, 236),
    rgb(247, 68, 45)
  );
  color: inherit;
  border-radius: 20px;
  transition: 0.3s;
  cursor: pointer;
}
.sideBTN:hover {
  transform: scale(110%) rotate(-2deg);
}
.btnallCateg {
  margin-bottom: 16px;
  width: 100%;
}
.btnCateg {
  width: 100%;
  background-image: none;
}
.btnLarge {
  font-size: 1px;
  padding: 20px 32px 17px;
}

main {
  display: grid;
  grid-column: auto;
}

@media (max-width: 900px) {
  .main {
    grid-template-columns: 1fr;
    height: auto;
  }
}

@media (max-width: 600px) {
  #root {
    padding: 24px;
  }

  .header {
    flex-direction: column;
    gap: 16px;
    /* size: 16px; */
  }

  .header h1 {
    font-size: 25px;
  }

  .fact-form {
    padding: 16px;
  }

  .fact {
    padding: 12px 16px;
  }
}
